@use '../../abstracts/variables/colors' as *;
@use '../../abstracts/variables/size' as *;
@use '../../abstracts/mixins/mixins' as *;

.footer {
    padding: 0 28px;

    .container {

        span {
            color: $setting-description;
            text-overflow: ellipsis;
            font-size: 14px;
            line-height: $line-height;
            display: block;
        }

        a {
            color: $setting-description;
        }
    }
}

// Remove extra blank space under the plugin content on OneTap admin pages.
// WordPress core adds bottom padding on #wpbody-content to make room for the
// default admin footer, which we don't use here, so it appears as empty space.
body.wp-admin[class*="accessibility-onetap"] #wpbody-content,
body.wp-admin[class*="apop-settings"] #wpbody-content,
body.wp-admin[class*="onetap-pro"] #wpbody-content,
body.wp-admin[class*="apop-module-labels"] #wpbody-content {
    overflow: clip;
}

@media only screen and (max-width: 768px) {

    .footer {
        padding: 0 15px;
    }
}