@use '../../abstracts/variables/colors' as *;

.settings-group.current-language {

    .setting-control.module-labels {
        padding-top: 12px !important;
        padding-bottom: 12px !important;

        > .setting-title,
        .boxes {
            display: none;
        }
    }
} 

.box-current-language {
    flex-wrap: wrap;
    justify-content: space-between;

    a.current-language {
        text-decoration: none;
        cursor: pointer;
        display: flex;
        flex-wrap: wrap;
        gap: 16px;
        justify-content: flex-start;
        align-items: center;
        padding: 0;
        border-radius: 0.5rem 0.5rem 0.5rem 0.5rem;

        .image {

            img {
                width: 20px;
                height: auto;
                display: none;

                &.active {
                    display: block;

                }
            }
        }

        .text-current-language {
            margin: 0;
            color: $setting-description;
            font-size: 14px;
            font-style: normal;
            font-weight: 400;
            line-height: 20px;           

            strong {
                color: $button-color;
                font-size: 14px;
                font-style: normal;
                font-weight: 600;
                line-height: 20px;           
            }
        }

        &:focus {
            border: none !important;
            box-shadow: none !important;
            outline: 0 !important;
        }
    }

    a.change-language {
        display: flex;
        padding: 10px 14px;
        justify-content: center;
        align-items: center;
        gap: 4px;
        border-radius: 8px;
        border: 1px solid $button-outline-border-color;
        background: $light-bg;
        box-shadow: 0 0 0 1px rgba(10, 13, 18, 0.18) inset, 0 -2px 0 0 varrgba(10, 13, 18, 0.05) inset, 0 1px 2px 0 rgba(10, 13, 18, 0.05);
        color: $control-text;
        font-size: 14px;
        font-style: normal;
        font-weight: 600;
        line-height: 20px;     
        text-decoration: none;
    }
}

@media only screen and (max-width: 1200px) {

    .box-current-language {

        a.current-language {
            flex-wrap: nowrap;
            align-items: flex-start;

            .image {
                position: relative;
                top: 6px;
            }
        }
    }
}