@use '../../abstracts/variables/colors' as *;
@use '../../abstracts/variables/size' as *;
@use '../../abstracts/mixins/mixins' as *;

.header-nav-link {
	font-size: 14px;
	display: inline-block;
	padding: 8px 12px;
	text-decoration: none;
    color: $setting-title;
    font-weight: 400;
    border-radius: 8px;
    transition: background-color 0.1s ease, border-color 0.1s ease;
    border: 1px solid transparent;	

	&:hover {
		background: $nav-link-hover-bg;
		border: 1px solid $nav-link-hover-bg;
		color: $setting-title;	
	}

	&:focus {
		box-shadow: none;
		outline: none;
		border-radius: 8px;
		background: $nav-link-selected-bg;
		border: 1px solid $nav-link-selected-bg;
	}		

	&:active {
		background: $nav-link-selected-bg;
		border: 1px solid $nav-link-selected-bg;
		color: $setting-title;
	}

	&.active {
		border-radius: 8px;
		color: $setting-title;
		background: $nav-link-selected-bg;
		border: 1px solid $nav-link-selected-bg;
		font-weight: 500;		
	}

}

body.wp-admin {

	.wrap {

		a.header-nav-link {

			&:focus {
				border: 1px solid transparent;
				color: $setting-title;
			}
		}
	}
}