@use '../../abstracts/variables/colors' as *;
@use '../../abstracts/variables/size' as *;

/**
 * Toggle Switch Placeholder
 * Provides consistent styling for toggle switch components
 */
%toggle-switch {
    display: inline-block;
    width: 44px;
    height: 24px;
    padding: 2px;

    input {
        display: none;
    }

    input:not(:checked) + .slider:hover {
        background: $input-border-color-hover;
        transition: background-color 0.01s ease-out;
    }

    .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: $input-border-color;
        -webkit-transition: 0.4s;
        transition: 0.4s;
        margin: 0;
    }

    .slider::before {
        content: "";
        position: absolute;
        left: 3px;
        bottom: 4px;
        -webkit-transition: .4s;
        transition: .4s;
        height: 20px;
        width: 20px;
        border-radius: 9999px;
        background: $light-bg;
    }

    input:checked + .slider {
        background: $primary-color;
        border-radius: 50px;
    }

    input:checked + .slider:hover {
        background: #023fdc;
        transition: background-color 0.08s ease-out;
    }

    input:focus + .slider {
        border-radius: 50px;
    }

    input:checked + .slider::before {
        -webkit-transform: translateX(22px);
        -ms-transform: translateX(22px);
        transform: translateX(22px);
    }

    .slider.round {
        border-radius: 50px;
        width: 48px;
    }
}

/**
 * Form Label Placeholder  
 * Provides consistent styling for form labels (more prominent than setting labels)
 * Usage: @extend %form-label;
 */
%form-label {
    color: $setting-title;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: $line-height;
}

/**
 * Form Input Placeholder
 * Provides consistent styling for form input fields
 * Usage: @extend %form-input;
 */
%form-input {
    border-radius: 8px;
    border: 1px solid $border-secondary-color;
    padding: 10px 14px;
    border-color: $input-border-color;
    box-shadow: 0 1px 2px 0 rgba(10, 13, 18, .05);
    overflow: hidden;
    color: $text-color;
    text-overflow: ellipsis;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    line-height: 20px;
    width: 100%;
    max-width: 100%;
    height: auto;
    min-height: auto;
}

/**
 * Setting Title Placeholder
 * Provides consistent styling for setting section titles
 * Usage: @extend %setting-title;
 */
%setting-title {
    color: $setting-title;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 28px;
    display: block;
}

/**
 * Setting Description Placeholder
 * Provides consistent styling for setting descriptions with bottom border
 * Usage: @extend %setting-description;
 */
%setting-description {
    overflow: hidden;
    color: $setting-description;
    text-overflow: ellipsis;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: $line-height;
    display: block;
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid $border-color-light;

    // Link styling within description
    a {
        color: $primary-color;
        text-decoration: none;
        border-bottom: solid 1px;
    }

    a:hover {
        border-bottom: solid 2px;
    }

    a:focus {
        color: $primary-color;
        text-decoration: none;
        border-bottom: solid 2px;
        border-top: solid 0;
        border-right: solid 0;
        border-left: solid 0;
    }
}

/**
 * Control Heading Placeholder
 * Provides consistent styling for control section headings
 * Usage: @extend %control-heading;
 */
%control-heading {
    font-size: 14px;
    font-style: normal;
    line-height: $line-height;
    padding: 20px 0;
    display: block;
    font-weight: 500;
    color: $setting-title;
}

/**
 * Button Primary Placeholder
 * Provides consistent styling for primary button components
 * Usage: @extend %button-primary;
 */
%button-primary {
    cursor: pointer;
    border: 1px solid $border-secondary-color;
    background: $light-bg;
    border-radius: 8px;
    display: flex;
    flex-wrap: wrap;
    gap: 6px;
    padding: 10px 16px;
    color: $button-color;
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    text-decoration: none;
}

%devices-tabs {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 4px;
    gap: 4px;
    border-radius: 10px;
    border: none;
    background: $nav-link-selected-bg;

    button {
        display: flex;
        height: 36px;
        padding: 8px 12px;
        justify-content: center;
        align-items: center;
        gap: 8px;
        border-radius: 6px;
        border: none;
        width: 33%;
        cursor: pointer;
        background: $nav-link-selected-bg;

        font-size: 14px;
        font-style: normal;
        line-height: $line-height;
        font-weight: 500;
        color: $setting-title;

        &:hover,
        &.active {
            background: $light-bg;
            box-shadow: 0 1px 3px 0 var(--Colors-Effects-Shadows-shadow-sm_01, rgba(10, 13, 18, 0.10)), 0 1px 2px -1px var(--Colors-Effects-Shadows-shadow-sm_02, rgba(10, 13, 18, 0.10));
        }
    }

    @media only screen and (max-width: 480px) {

        button {
            gap: 4px;
        }
    }
}


/**
 * Default Label Placeholder
 * Provides consistent styling for default label text in dropdown inputs
 * Usage: @extend %default-label;
 */
 %default-label {
    display: inline-block;
    padding: 2px 6px;
    border-radius: 6px;
    border: 1px solid $border-secondary-color;
    background: $light-bg;
    color: $button-color;
    text-align: center;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    position: absolute;
    top: 9px;
    right: 36px;
    left: auto;
    max-width: 80px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

/**
 * Dropdown Options Popup Placeholder
 * Provides consistent styling for dropdown options popup (fade/slide animation)
 * Usage: @extend %dropdown-options-popup;
 */
%dropdown-options-popup {
    position: absolute;
    top: calc(100% + 4px);
    left: 0;
    right: 0;
    background: $light-bg;
    border: 1px solid $border-secondary-color;
    box-shadow: 0 4px 6px -1px #0000001a, 0 2px 4px -2px #0000001a;
    border-radius: 8px;
    list-style: none;
    margin: 0;
    padding: 3px 6px;
    z-index: 99;
    // Always keep element in DOM for smooth animation
    display: block;
    max-height: 300px;
    overflow-y: auto;
    // Fade / slide animation
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transform: translateY(4px);
    transition: opacity 0.3s ease, transform 0.3s ease, visibility 0.3s ease;
}

/**
 * Dropdown Option Item Placeholder
 * Provides consistent styling for dropdown option items (list items)
 * Usage: @extend %dropdown-option-item;
 */
%dropdown-option-item {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 8px 12px;
    cursor: pointer;
    transition: all .015s ease;
    position: relative;
    margin: 3px 0;
    border-radius: 8px;

    &:hover {
        background: $secondary-bg;
    }

    &.selected {
        background: $secondary-bg;
    }
}