@use '../abstracts/placeholders/placeholders' as *;
@use '../abstracts/variables/colors' as *;
@use '../abstracts/variables/size' as *;

.wrap {

    form {

        .mycontainer {
            display: flex;
            flex-wrap: wrap;
            align-items: flex-start;
            gap: 20px;

            // Left
            .left-column {
                width: 320px;
                padding: 24px;
                border-radius: 16px;
                background: $light-bg;

                // Show Accessibility Statement
                .box-control-switch {
                    display: flex;
                    flex-wrap: wrap;
                    align-items: center;
                    gap: 12px;
                    border-bottom: 1px solid $border-color-light;
                    padding-bottom: 22px;
                    margin-bottom: 22px;

                    svg {
                        padding: 8px;
                        border-radius: 6px;
                        border: 1px solid $border-secondary-color;
                        background: $light-bg;
                        width: 16px;
                        height: 16px;
                    }

                    .box-swich {
                        position: relative;
                        width: calc(100% - 46px);

                        label.switch {
                            display: flex;
                            flex-wrap: wrap;
                            align-items: center;
                            width: auto;
                            padding-right: 40px;

                            @extend %toggle-switch;

                            .slider.round {
                                left: unset;
                                top: 4px;
                                right: 0;
                                width: 36px;
                                height: 20px;
                            }

                            .slider::before {
                                height: 16px;
                                width: 16px;
                                bottom: 2px;
                                left: 2px;
                            }

                            input:checked + .slider::before {
                                -webkit-transform: translateX(16px);
                                -ms-transform: translateX(16px);
                                transform: translateX(16px);
                            }
                        }
                    }

                    .label {
                        color: $setting-title;
                        font-weight: 500;
                    }
                }

                // Company Website
                .box-control-company-website {
                    position: relative;

                    .protocol {
                        display: flex;
                        display: none;
                        flex-wrap: wrap;
                        align-items: center;
                        position: absolute;
                        color: $setting-description;
                        font-size: 16px;
                        font-style: normal;
                        font-weight: 400;
                        line-height: 24px;
                        padding: 10px 14px;
                        height: 26px;
                        border: none;
                        border-right: 1px solid $border-secondary-color;

                        &.focus {
                            border-right: 2px solid $primary-hover-color;
                        }
                    }
                }

                // Confirmation Checkbox
                .box-control-confirm {
                    position: relative;

                    input {
                        margin: 6px 0 6px 0;
                        border: 1px solid $border-secondary-color;
                        border-radius: 6px;
                        width: 20px;
                        height: 20px;
                        margin-right: 12px;
                        position: absolute;
                        top: 0;
                        left: 0;

                        &::before {
                            position: relative;
                            top: 2px;
                            left: 2px;
                        }
                    }

                    .text {
                        display: block;
                        padding-left: 32px;
                    }
                
                    label {
                        font-size: 12px;
                        line-height: 16px;
                        color: $setting-description;
                        font-weight: 400;
                    }
                }

                // Custom box controls
                .custom-box-controls {

                    label {
                        display: block;
                        margin-bottom: 6px;
                    }
                }

                // Language select dropdown
                .language-select-dropdown {
                    position: relative;
                    width: 100%;

                    .language-select-input {
                        display: none;
                    }

                    .language-select-wrapper {
                        position: relative;
                    }

                    .language-select-trigger {
                        width: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        background: $light-bg;
                        cursor: pointer;
                        border: 1px solid $border-secondary-color;
                        border-radius: 8px;
                        transition: all 0.2s ease;
                        padding: 10px 14px;
                        border-color: $input-border-color;                     

                        @extend %form-input;

                        color: $setting-title;
                        font-size: 14px;
                        font-weight: 400;
                        line-height: 20px;

                        &:hover {
                            border-color: $input-border-color-hover;
                            transition:
                            border-color 0.1s ease-out,
                            background-color 0.1s ease-out,
                            box-shadow 0.1s ease-out;
                        }

                        &:focus {
                            border: 1px solid $primary-color;
                            box-shadow: 0 0 0 1px $primary-color;
                            outline: none;
                        }

                        &[aria-expanded="true"] {

                            .language-select-chevron {
                                transform: rotate(180deg);
                            }
                        }

                        .language-select-selected {
                            display: flex;
                            align-items: center;
                            gap: 8px;
                            flex: 1;

                            .language-select-flag {
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                width: 20px;
                                height: 20px;
                                flex-shrink: 0;

                                img {
                                    width: 100%;
                                    height: 100%;
                                    object-fit: contain;
                                    display: block;
                                }
                            }

                            .language-select-label {
                                overflow: hidden;
                                text-overflow: ellipsis;
                                white-space: nowrap;
                                color: $setting-title;
                                font-size: 14px;
                                font-weight: 400;
                                line-height: 20px;
                            }
                        }

                        .language-select-chevron {
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            width: 16px;
                            height: 16px;
                            flex-shrink: 0;
                            transition: transform 0.2s ease;
                        }
                    }

                    .language-select-options {

                        @extend %dropdown-options-popup;

                        &.open {
                            opacity: 1;
                            visibility: visible;
                            pointer-events: auto;
                            transform: translateY(0);
                        }

                        .language-select-option {

                            @extend %dropdown-option-item;

                            &.selected {

                                .language-select-checkmark {
                                    display: flex;
                                }
                            }

                            .language-select-option-flag {
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                width: 20px;
                                height: 20px;
                                flex-shrink: 0;

                                img {
                                    width: 100%;
                                    height: 100%;
                                    object-fit: contain;
                                    display: block;
                                }
                            }

                            .language-select-option-label {
                                color: $control-text;
                                font-size: 14px;
                                font-style: normal;
                                font-weight: 500;
                                line-height: 20px;
                                flex: 1;
                            }

                            .language-select-checkmark {
                                display: none;
                                align-items: center;
                                justify-content: center;
                                width: 16px;
                                height: 16px;
                                flex-shrink: 0;
                                margin-left: auto;
                            }
                        }
                    }

                    &:hover {
                        border-color: $input-border-color-hover;
                        transition:
                        border-color 0.1s ease-out,
                        background-color 0.1s ease-out,
                        box-shadow 0.1s ease-out;
                    }
                }

                // Generate Accessibility Statement
                button.generate-accessibility-statement {
                    display: flex;
                    flex-wrap: wrap;
                    padding: 12px 18px;
                    justify-content: center;
                    align-items: center;
                    gap: 6px;
                    border-radius: 8px;
                    background: $primary-color;
                    color: $light-color;
                    font-size: 16px;
                    font-style: normal;
                    font-weight: 600;
                    line-height: 24px;
                    width: 100%;
                    margin-top: 0;
                    border: none;
                    box-shadow: none;

                    &:hover {
                        background: $primary-hover-color;
                        background: #023FDC;
                    }
                }

                .note {
                    position: absolute;
                    left: 28px;
                    margin-top: calc(24px + 18px);
                    padding-right: 58px;
                    width: calc(392px - 24px);
                    display: block;
                    color: $setting-description;
                    font-size: 14px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 20px;
                }
            }

            // Right
            .right-column {
                width: calc(100% - 404px - 48px);
                padding: 32px;
                border-radius: 16px;
                background: $light-bg;

                // Box information
                .box-information {

                    .setting-title {

                        @extend %setting-title;

                        font-weight: 500;
                    }

                    .setting-description {

                        @extend %setting-description;

                        color: $feature-desc-color;
                    }
                }

                // Box editor
                .box-editor {

                    .wp-editor-wrap {

                        .wp-editor-container {
                            border: none;

                            .mce-toolbar-grp.mce-panel {
                                border: none;
                                background: none;
                                padding: 12px 0;
                                margin-bottom: 0;
                            }

                            .mce-top-part {

                                &::before {
                                    border: none;
                                    box-shadow: none;
                                }
                            }

                            .mce-edit-area {
                                margin-bottom: 0;
                                outline: 1px solid $border-color-light;
                                padding: 24px;
                                border-radius: 20px;                                
                            }

                            .mce-resizehandle,
                            .mce-path.mce-flow-layout-item.mce-first {
                                display: none;
                            }

                            .mce-statusbar.mce-container.mce-panel.mce-stack-layout-item.mce-last {
                                border: none;
                            }
                        }
                    }
                }
            }
        }
    }

    p.submit {
        display: none;
    }
}

@media only screen and (max-width: 1200px) {

    .wrap {

        form {

            .mycontainer {

                .left-column,
                .right-column {
                    width: 100%;
                    overflow: hidden;
                }

                .left-column {

                    .note {
                        position: static;
                        width: 100%;
                        margin: 15px 0 0 0;
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 768px) {

    .wrap {

        form {

            .mycontainer {
                gap: 15px;
                
                .left-column,
                .right-column {
                    padding: 15px;
                }

                .left-column {

                    .box-control-confirm {

                        input::before {
                            top: -2px;
                            left: -2px;
                        }
                    }
                }
            }
        }
    }
}