@use '../abstracts/variables/colors' as *;
@use '../abstracts/variables/size' as *;
@use '../abstracts/mixins/mixins' as *;
@use '../abstracts/placeholders/placeholders' as *;

.setting-control.widget-position {

    .boxes {

        .box {
            position: relative;

            .widget-position-dropdown {
                position: relative;
                width: 100%;

                .widget-position-input {
                    display: none;
                }

                .widget-position-wrapper {
                    display: flex;
                    align-items: center;
                    gap: 8px;
                    position: relative;
                }

                .widget-position-trigger {
                    flex: 1;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    background: $light-bg;
                    cursor: pointer;
                    padding: 10px 14px;
                    border-color: $input-border-color;                       

                    @extend %form-input;

                    color: $setting-title;
                    font-size: 14px;
                    font-weight: 400;
                    line-height: 20px;

                    &:focus {
                        border: 1px solid $primary-color;
                        box-shadow: 0 0 0 1px $primary-color;
                    }

                    &:hover {
                        padding: 10px 14px;
                        border-color: $input-border-color-hover;
                        transition:
                            border-color 0.1s ease-out,
                            background-color 0.1s ease-out,
                            box-shadow 0.1s ease-out;
                    }

                    &[aria-expanded="true"] {

                        .widget-position-chevron {
                            transform: rotate(180deg);
                        }
                    }

                    .widget-position-selected {
                        display: flex;
                        align-items: center;
                        gap: 8px;
                        flex: 1;

                        .widget-position-icon {
                            display: flex;
                            align-items: center;
                            justify-content: center;
                        }

                        .widget-position-label {
                            overflow: hidden;
                            color: $setting-title;
                            text-overflow: ellipsis;
                            font-size: 14px;
                            font-weight: 400;
                            line-height: 20px;
                        }
                    }

                    .widget-position-chevron {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        width: 16px;
                        height: 16px;
                        transition: transform 0.2s ease;
                    }
                }

                .widget-position-options {

                    @extend %dropdown-options-popup;

                    &.open {
                        opacity: 1;
                        visibility: visible;
                        pointer-events: auto;
                        transform: translateY(0);
                        padding: 3px 6px 3px 6px;
                    }                    

                    .widget-position-option {
                        margin-top: 3px;
                        margin-bottom: 3px;
                        margin-left: 0;
                        margin-right: 0;
                        padding: 8px 12px;

                        @extend %dropdown-option-item;          
                        
                        &.selected {

                            .widget-position-checkmark {
                                display: flex;
                            }

                            &:nth-child(3),
                            &:nth-child(5) {

                                &::before {
                                    top: -4px;
                                }
                            }
                            
                        }

                        .widget-position-option-icon {
                            width: 16px;
                            height: 16px;
                        }

                        .widget-position-option-label {
                            font-size: 14px;
                            font-style: normal;
                            line-height: 20px;
                            color: $setting-title;
                            font-weight: 400;                            
                        }

                        .widget-position-checkmark {
                            display: none;
                            align-items: center;
                            justify-content: center;
                            width: 16px;
                            height: 16px;
                            margin-left: auto;
                        }
                    }

                    .widget-position-option:not(.selected):hover {
                        background: $nav-link-selected-bg;
                    }

                    hr {
                        border: none;
                        height: 1px;
                        background: $border-secondary-color;
                        margin: 6px 0;
                        width: 100%;
                    }
                }

                .widget-label-default {

                    @extend %default-label;
                }
            }
        }
    }
}

