@use '../abstracts/variables/colors' as *;
@use '../abstracts/variables/size' as *;
@use '../abstracts/mixins/mixins' as *;
@use '../abstracts/placeholders/placeholders' as *;

.setting-control.language-select-toggles {

    display: none;

    .boxes {

        .box {
            position: relative;

            .language-select-dropdown {
                position: relative;
                width: 100%;

                .language-select-input {
                    display: none;
                }

                .language-select-labels-wrapper {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    margin-bottom: 12px;

                    .language-select-label-default {
                        color: $setting-title;
                        font-size: 14px;
                        font-style: normal;
                        font-weight: 500;
                        line-height: 20px;
                    }

                    .language-select-label-display-options {
                        display: flex;
                        align-items: center;
                        gap: 6px;
                        color: $setting-title;
                        font-size: 14px;
                        font-style: normal;
                        font-weight: 500;
                        line-height: 20px;
                        cursor: pointer;
                        transition: color 0.3s ease;
                        user-select: none;

                        &:hover {
                            color: $primary-color;
                            text-decoration: underline;
                        }
                    }
                }

                .language-select-wrapper {
                    display: flex;
                    align-items: center;
                    gap: 8px;
                    position: relative;
                }

                .language-select-trigger {
                    flex: 1;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    background: $light-bg;
                    cursor: pointer;
                    padding: 10px 14px;
                    border-color: $input-border-color;                    

                    @extend %form-input;

                    color: $setting-title;
                    font-size: 14px;
                    font-weight: 400;
                    line-height: 20px;

                    &:hover {
                        border-color: $input-border-color-hover;
                        transition:
                        border-color 0.1s ease-out,
                        background-color 0.1s ease-out,
                        box-shadow 0.1s ease-out;
                    }                    

                    &:focus {
                        border: 1px solid $primary-color;
                        box-shadow: 0 0 0 1px $primary-color;
                    }

                    &[aria-expanded="true"] {

                        .language-select-chevron {
                            transform: rotate(180deg);
                        }
                    }

                    .language-select-selected {
                        display: flex;
                        align-items: center;
                        gap: 8px;
                        flex: 1;

                        .language-select-flag {
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            width: 20px;
                            height: 20px;
                            flex-shrink: 0;

                            img {
                                width: 100%;
                                height: 100%;
                                object-fit: contain;
                                display: block;
                            }
                        }

                        .language-select-label {
                            overflow: hidden;
                            text-overflow: ellipsis;
                            white-space: nowrap;                         
                            color: $setting-title;
                            font-size: 14px;
                            font-weight: 400;
                            line-height: 20px;                            
                        }
                    }

                    .language-select-chevron {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        width: 16px;
                        height: 16px;
                        flex-shrink: 0;
                        transition: transform 0.3s ease;
                    }
                }

                .language-select-options {

                    @extend %dropdown-options-popup;

                    &.open {
                        opacity: 1;
                        visibility: visible;
                        pointer-events: auto;
                        transform: translateY(0);
                    }

                    .language-select-option {

                        @extend %dropdown-option-item;

                        .language-select-option-flag {
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            width: 20px;
                            height: 20px;
                            flex-shrink: 0;

                            img {
                                width: 100%;
                                height: 100%;
                                object-fit: contain;
                                display: block;
                            }
                        }

                        .language-select-option-label {
                            color: $control-text;
                            font-size: 14px;
                            font-style: normal;
                            font-weight: 500;
                            line-height: 20px;
                            flex: 1;
                        }

                        .language-select-option-default-label {

                            @extend %default-label;

                            display: none;
                            right: 10px;
                            top: 9px;
                        }

                        .box-swich {
                            position: relative;
                            flex-shrink: 0;
                            margin-left: auto;
                            width: 40px;
                            height: 20px;

                            &.show {
                                display: block
                            }

                            label.switch {

                                @extend %toggle-switch;

                                .slider.round {
                                    width: 40px;
                                    height: 20px;
                                }

                                .slider::before {
                                    left: 3px;
                                    bottom: 2px;
                                    height: 16px;
                                    width: 16px;
                                }

                                input:checked+.slider::before {
                                    transform: translateX(19px);
                                }

                                input:checked+.slider:hover {
                                    background: #023fdc;
                                }
                            }
                        }

                        &.selected {

                            .language-select-option-default-label {
                                display: block;
                            }

                            .box-swich {
                                display: none;
                            }
                        }
                    }
                }

                .language-select-actions {
                    display: none;
                    align-items: center;
                    justify-content: end;
                    gap: 12px;
                    padding: 13px 16px;
                    padding-right: 42px;
                    padding-right: 24px;
                    position: absolute;
                    top: calc(100% + 289px + 22px);
                    left: 0;
                    right: 0;
                    background: $light-bg;
                    border-radius: 0 0 8px 8px;
                    border: 1px solid $border-secondary-color;
                    box-shadow: 0 1px 2px 0 rgba(10, 13, 18, .05);
                    z-index: 999;
                    margin-top: 0;

                    .language-disable-all,
                    .language-enable-all {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        font-style: normal;
                        cursor: pointer;
                    }

                }

                .language-label-default {

                    @extend %default-label;
                }

                .language-select-check-icon {
                    display: none;
                }

                .language-select-options.open {
                    border-radius: 8px 8px 0 0;
                    border: 1px solid $border-secondary-color;
                    box-shadow: 0 4px 6px -1px #0000001a, 0 2px 4px -2px #0000001a;

                    ~ .language-select-actions {
                        display: flex;
                    }
                }

                &:hover {
                    border-color: $input-border-color-hover;
                    transition:
                    border-color 0.1s ease-out,
                    background-color 0.1s ease-out,
                    box-shadow 0.1s ease-out;
                }
            }
        }
    }
}

