@use '../abstracts/variables/colors' as *;
@use '../abstracts/variables/size' as *;
@use '../abstracts/mixins/mixins' as *;
@use '../abstracts/placeholders/placeholders' as *;

.setting-control.language-select-simple {

    .boxes {

        .box {
            position: relative;

            .language-select-dropdown {
                position: relative;
                width: 100%;
                padding-left: 0;

                .language-select-input {
                    display: none;
                }

                .language-select-labels-wrapper {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    margin-bottom: 12px;

                    .language-select-label-default {
                        color: $setting-title;
                        font-size: 14px;
                        font-style: normal;
                        font-weight: 500;
                        line-height: 20px;
                    }

                    .language-select-label-display-options {
                        display: flex;
                        align-items: center;
                        gap: 6px;
                        color: $setting-title;
                        font-size: 14px;
                        font-style: normal;
                        font-weight: 500;
                        line-height: 20px;
                        cursor: pointer;
                        transition: color 0.3s ease;
                        user-select: none;

                        &:hover {
                            color: $primary-color;
                            text-decoration: underline;
                        }
                    }
                }

                .language-select-wrapper {
                    display: flex;
                    align-items: center;
                    gap: 8px;
                    position: relative;
                }

                .language-select-trigger {
                    flex: 1;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    background: $light-bg;
                    cursor: pointer;
                    padding: 10px 14px;
                    border-color: $input-border-color;                    

                    @extend %form-input;

                    color: $setting-title;
                    font-size: 14px;
                    font-weight: 400;
                    line-height: 20px;

                    &:hover {
                        padding: 10px 14px;
                        border-color: $input-border-color-hover;
                        transition:
                        border-color 0.1s ease-out,
                        background-color 0.1s ease-out,
                        box-shadow 0.1s ease-out;
                    }                    

                    &:focus {
                        border: 1px solid $primary-color;
                        box-shadow: 0 0 0 1px $primary-color;
                    }

                    &[aria-expanded="true"] {

                        .language-select-chevron {
                            transform: rotate(180deg);
                        }
                    }

                    .language-select-selected {
                        display: flex;
                        align-items: center;
                        gap: 8px;
                        flex: 1;

                        .language-select-flag {
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            width: 20px;
                            height: 20px;
                            flex-shrink: 0;

                            img {
                                width: 100%;
                                height: 100%;
                                object-fit: contain;
                                display: block;
                            }
                        }

                        .language-select-label {
                            overflow: hidden;
                            text-overflow: ellipsis;
                            white-space: nowrap;
                            color: $setting-title;
                            font-size: 14px;
                            font-weight: 400;
                            line-height: 20px;
                        }
                    }

                    .language-select-chevron {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        width: 16px;
                        height: 16px;
                        flex-shrink: 0;
                        transition: transform 0.3s ease;
                    }
                }

                .language-select-options {

                    @extend %dropdown-options-popup;

                    &.open {
                        opacity: 1;
                        visibility: visible;
                        pointer-events: auto;
                        transform: translateY(0);
                    }

                    .language-select-option {

                        @extend %dropdown-option-item;

                        &.selected {

                            .language-select-check-icon {
                                display: block;
                            }
                        }

                        .language-select-option-flag {
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            width: 20px;
                            height: 20px;
                            flex-shrink: 0;

                            img {
                                width: 100%;
                                height: 100%;
                                object-fit: contain;
                                display: block;
                            }
                        }

                        .language-select-option-label {
                            font-size: 14px;
                            font-style: normal;
                            line-height: 20px;
                            flex: 1;
                            color: $setting-title;
                            font-weight: 400;
                        }

                        .language-select-check-icon {
                            display: none;
                        }

                        .language-select-option-default-label {
                            display: none;
                        }
                    }
                }

                .language-label-default {

                    @extend %default-label;
                }

                &.display-options-active {

                    .language-select-wrapper {

                        .language-select-chevron {
                            transform: rotate(180deg);                
                        }
                    }

                    .language-select-options {
                        display: block;
                        // Ensure visible when display options panel is active
                        opacity: 1;
                        visibility: visible;
                        pointer-events: auto;
                        transform: translateY(0);
                        border-radius: 8px 8px 0 0;

                        .language-select-option {
                            background: $light-bg;

                            .language-select-check-icon {
                                display: none;
                            }

                            &:hover {
                                background: $secondary-bg;
                            }

                            &.selected {

                                background: $secondary-bg;

                                .language-select-option-default-label {

                                    @extend %default-label;

                                    display: inline-block;
                                    right: 10px;
                                    top: 9px;
                                }
                            }
                        }
                    }
                }

                &:hover {
                    padding-left: 0;
                    border-color: $input-border-color-hover;
                    transition:
                    border-color 0.1s ease-out,
                    background-color 0.1s ease-out,
                    box-shadow 0.1s ease-out;
                }
            }
        }
    }
}

