@use '../../abstracts/variables/colors' as *;

.button.outline,
.buttons.button.outline,
.wp-core-ui .button.outline {
    border: none;
    color: #1f2937;
    padding: 10px 14px;
    font-weight: 500;
    border-radius: 8px;
    box-shadow:
        0 0 0 1px #e5e7eb inset,
        0 -2px 0 rgba(10,13,18,0.05) inset,
        0 1px 2px rgba(10,13,18,0.05);
}

/* Outline button  hover & focus */
.button.outline:hover,
.buttons.button.outline:hover,
.wp-core-ui .button.outline:hover,
.button.outline:focus,
.buttons.button.outline:focus,
.wp-core-ui .button.outline:focus {
    box-shadow:
        0 0 0 1px #d1d5db inset,
        0 -2px 0 rgba(10,13,18,0.05) inset,
        0 1px 2px rgba(10,13,18,0.05);
    background: #f9fafb;
} 

.edit-labels.button.outline {
    border: 1px solid transparent;
}

.mce-toolbar .mce-btn-group {
    background: $light-bg;
    box-shadow: 0 10px 15px -3px rgb(0 0 0 / 5%), 0 4px 6px -4px rgb(0 0 0 / 5%);
    border-radius: 12px;
    padding: 6px;
    border: 1px solid #e9eaeb;
}


.mce-toolbar .mce-btn-group .mce-btn, .qt-dfw {
    border: 1px solid transparent;
    margin: 2px;
    background: $light-bg;
    padding: 4px !important;
	border-radius: 8px !important;
}

.mce-toolbar .mce-btn-group .mce-btn:hover {
    background: #e9eaeb !important;
	box-shadow: none !important;
	border-radius: 8px !important;
}

.mce-toolbar .mce-btn-group .mce-btn:focus,
.mce-toolbar .mce-btn-group .mce-btn:hover,
.qt-dfw:focus,
.qt-dfw:hover {
    background: #d1d5db;
    color: #1d2327;
    box-shadow: none !important;
    outline: unset !important;
	border: solid 1px transparent !important;
}

.mce-toolbar .mce-btn-group .mce-btn.mce-active,
.mce-toolbar .mce-btn-group .mce-btn:active,
.qt-dfw.active {
    border-color: transparent !important;
}

.mce-toolbar .mce-btn i {
    color: #374151;
}

.mce-toolbar .mce-btn-group {
    padding: 4px !important;
}

.wrap {
    background: #f0f0f1;

    input,
    select {
        padding: 10px 14px;
        border-color: $input-border-color;
        color: $setting-title;
        font-size: 14px;
        font-weight: 400;
        line-height: 20px;
    }   
}

body.wp-admin {

    .wrap {

        .options-wrapper {

            input:focus,
            select:focus {
                border: 1px solid $primary-color;
                box-shadow: 0 0 0 1px $primary-color;
            }
        }
    }
}