@use '../../abstracts/variables/colors' as *;
@use '../../abstracts/variables/size' as *;
@use '../../abstracts/mixins/mixins' as *;

.wp-core-ui .button,
.buttons.button,
.button {
	font-weight: 600;
	font-size: 14px;
	line-height: $line-height;
	color: $button-color;
	padding: 10px 16px;
	margin: 0;
	border: 1px solid $primary-color;
	border-radius: 8px;
	background: transparent;
	text-decoration: none;
	display: inline-block;
	height: auto;

	&:hover {
		background: $primary-hover-color;
		background: #023FDC;
	}

	&.outline {
		border: 1px solid $button-outline-border-color;
		background: transparent;
		color: $button-color;

		&:focus {
			box-shadow: none;
			outline: 2px solid transparent;
			border: 1px solid $primary-hover-color;
		}
	}

	&.solid {
		background: $primary-color;
		color: $light-color;
		border-color: $primary-color;
	}
}

.button.get-pro {
    cursor: pointer;
    border: 2px solid rgba(255, 255, 255, 0.12);
    border: none;
    background: $primary-color;
    color: $light-color;
    font-size: 14px;
    line-height: $line-height;
    outline: none;
    margin: 0;
    font-weight: 500;
    padding: 10px 14px;
    border-radius: 10px;
    box-shadow:
        0 0 0 1px #0048fe inset,
        0 -2px 0 rgba(10,13,18,0.05) inset,
        0 1px 2px rgba(10,13,18,0.05);

    &:hover {
		background: $primary-hover-color;
		background: #023FDC;
		color: $light-color;
	}
}

.save-changes.onetap-button-loading,
.onetap-button-loading {
	position: relative;
	color: transparent !important;
	z-index: 1;
	cursor: not-allowed;

    &::before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 18px;
        height: 18px;
        border-radius: 50%;
        border: 2px solid $light-color;
        border-top-color: transparent;
        animation: spin 1s infinite;
        transform: translate(-50%, -50%) rotate(0deg);
    }

	@keyframes spin {

		to {
		  transform: translate(-50%, -50%) rotate(360deg);
		}
	}
}
