@use '../../abstracts/variables/colors' as *;
@use '../../abstracts/variables/size' as *;
@use '../../abstracts/mixins/mixins' as *;

.fake-save-changes,
.save-changes {
    cursor: pointer;
    border: 2px solid rgba(255, 255, 255, 0.12);
    border: none;
    background: $primary-color;
    color: $light-color;
    font-size: 14px;
    line-height: $line-height;
    outline: none;
    margin: 0;
    font-weight: 500;
    padding: 10px 14px;
    border-radius: 8px;
    box-shadow:
        0 0 0 1px #0048fe inset,
        0 -2px 0 rgba(10,13,18,0.05) inset,
        0 1px 2px rgba(10,13,18,0.05);

    &:hover {
		background: $primary-hover-color;
		background: #023FDC;
	}

  &:active {
    background: #003ACB;
  }

  &.license-active {
    color: #596575;
    background: #fff;
    border: solid 1px #e3e9f1;
    box-shadow: none;

    &:hover {
      background: #023fdc;
      color: $light-color;
    }
  }
}